<template>
  <div class="newsbox">
    <GrayBar />

    <nut-infiniteloading
      @loadmore="onInfinite"
      :is-show-mod="true"
      :has-more="isHasMore"
      :is-loading="isLoading"
      :threshold="200"
    >
      <!-- <ul class="list">
        <li class="list-item" v-for="(item, index) of data" :key="index">我是测试数据 {{index + 1}}</li>
      </ul>-->

      <!-- list  -->
      <ul>
        <li v-for="(item, index) of data" :key="index">
          <h3>{{item.title}}</h3>
          <div class="box">
            <div class="timeBtn">发布时间</div>
            <p>{{item.time}}</p>
          </div>
        </li>
      </ul>
    </nut-infiniteloading>
  </div>
</template>

<script>
import GrayBar from "../components/GrayBar";
export default {
  name: "news",
  data() {
    return {
      data: [
        {
          title: "成考答题这几点要注意",
          time: "2020-07-06",
        },
        {
          title: "成考答题这几点要注意",
          time: "2020-07-06",
        },
        {
          title: "成考答题这几点要注意",
          time: "2020-07-06",
        },
        {
          title: "成考答题这几点要注意",
          time: "2020-07-06",
        },
        {
          title: "成考答题这几点要注意",
          time: "2020-07-06",
        },
        {
          title: "成考答题这几点要注意",
          time: "2020-07-06",
        },
        {
          title: "成考答题这几点要注意",
          time: "2020-07-06",
        },
        {
          title: "成考答题这几点要注意",
          time: "2020-07-06",
        },
        {
          title: "成考答题这几点要注意",
          time: "2020-07-06",
        },
        {
          title: "成考答题这几点要注意",
          time: "2020-07-06",
        },
      ],
      page: 0,
      num: 10,
      isHasMore: true,
      isLoading: false,
      isErr: false,
      timer: null,
    };
  },
  components: {
    GrayBar,
  },
  methods: {
    onInfinite() {
      this.isLoading = true;
      this.timer = setTimeout(() => {
        if (this.page <= 3) {
          this.data = this.data.concat(this.data);
          this.page = this.page + 1;
        } else {
          this.isHasMore = false;
        }
        this.isLoading = false;
      }, 1000);
    },
  },
};
</script>

<style lang="scss" scoped>
div.newsbox {
  padding-bottom: 30px;
  ul {
    padding: 0 19px;
    box-sizing: border-box;
    li {
      border-bottom: 1px solid rgba(209, 209, 209, 1);
      padding-bottom: 10px;
      h3 {
        font-size: 17px;
        font-weight: bold;
        color: rgba(0, 0, 0, 1);
        padding-top: 12px;
        width: 100%;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        padding-bottom: 11px;
      }
      div.box {
        display: flex;
        align-items: flex-end;
        div.timeBtn {
          font-size: 12px;
          font-weight: 400;
          height: 20px;
          line-height: 20px;
          border: 1px solid rgba(43, 142, 226, 1);
          border-radius: 10px;
          color: rgba(43, 142, 226, 1);
          padding: 0 9px;
          box-sizing: border-box;
          margin-right: 13px;
        }
        p {
          font-size: 12px;
          font-weight: 400;
          color: rgba(0, 0, 0, 1);
        }
      }
    }
  }
}
</style>